import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../shared/shared.module';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';


import { NavService } from './nav.service';
import { WebService } from './web.service';
import { UploadService } from '../../uploads/shared/upload.service';

import { UserLoginComponent } from '../user-login/user-login.component';
import { UserProfileComponent } from '../user-profile/user-profile.component';
import { UserFormComponent } from '../user-form/user-form.component';
import { TopNavComponent } from '../top-nav/top-nav.component';
import { FooterNavComponent } from '../footer-nav/footer-nav.component';
import { ReadmePageComponent } from '../readme-page/readme-page.component';
import { DashboardComponent } from '../dashboard/dashboard.component';
import { MyPiecesComponent } from '../my-pieces/my-pieces.component';
import { IGUploadComponent } from '../igupload/igupload.component';
import { NewPieceComponent } from '../my-pieces/new-piece/new-piece.component';
import { NewPieceDetailsComponent } from '../my-pieces/new-piece-details/new-piece-details.component';
import { PurchaseRequestsComponent } from '../purchase-requests/purchase-requests.component';
import { MyPieceComponent } from '../my-pieces/my-piece/my-piece.component';
import { SettingsComponent } from '../settings/settings.component';


@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    FormsModule,
    ReactiveFormsModule,
    RouterModule,
    BrowserAnimationsModule
  ],
  declarations: [
    // Unused components
    ReadmePageComponent,
    UserProfileComponent,

    // Base componets
    UserFormComponent,
    UserLoginComponent,
    TopNavComponent,
    FooterNavComponent,

    // Main components
    DashboardComponent,
    SettingsComponent,

    MyPiecesComponent,
    MyPieceComponent,
    IGUploadComponent,
    NewPieceComponent,
    NewPieceDetailsComponent,
    PurchaseRequestsComponent,
  ],
  exports: [
    TopNavComponent,
    FooterNavComponent,
    UserProfileComponent,
  ],
  providers: [
    UploadService,
    WebService,
  ]
})
export class UiModule { }



