import { Injectable } from '@angular/core';
import { Upload } from './upload';
import { AuthService } from '../../core/auth.service';
import { AngularFireDatabase, FirebaseListObservable, FirebaseObjectObservable } from 'angularfire2/database';
import * as firebase from 'firebase';


@Injectable()
export class UploadService {

  constructor(private db: AngularFireDatabase,
              private _authSrv: AuthService) { }

  private basePath:string = '/uploads';
  uploads: FirebaseListObservable<Upload[]>;


  getUploads(query={}) {
    this.uploads = this.db.list(this.basePath, {
      query: query
    });
    return this.uploads
  }


  deleteUpload(upload: Upload) {
    this.deleteFileData(upload.$key)
    .then( () => {
      this.deleteFileStorage(upload.name)
    })
    .catch(error => console.log(error))
  }

  // Executes the file uploading to firebase https://firebase.google.com/docs/storage/web/upload-files
  pushUpload(upload: Upload) {
    const storageRef = firebase.storage().ref();
    const uploadTask = storageRef.child(this.basePath + '/' + upload.file.name).put(upload.file);

    uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
      (snapshot) =>  {
        // upload in progress
        let snap = snapshot as firebase.storage.UploadTaskSnapshot
        upload.progress = (snap.bytesTransferred / snap.totalBytes) * 100
      },
      (error) => {
        // upload failed
        console.log(error)
      },
      () => {
        // upload success
        upload.url = uploadTask.snapshot.downloadURL
        upload.name = upload.file.name
        this.saveFileData(upload)
        return undefined
      }
    );
  }

  pieceUpload(upload: Upload) {
    return new Promise((res, rej) => {
      if(this._authSrv.authState) {
        console.log(this._authSrv.authState);
      }
      const storageRef = firebase.storage().ref();
      const uploadTask = storageRef.child('/uploads/' + this._authSrv.authState.uid + '/' + upload.file.name).put(upload.file);
      console.log(uploadTask)

      uploadTask.on(firebase.storage.TaskEvent.STATE_CHANGED,
        (snapshot) =>  {
          // upload in progress
          let snap = snapshot as firebase.storage.UploadTaskSnapshot
          upload.progress = (snap.bytesTransferred / snap.totalBytes) * 100
        },
        (error) => {
          // upload failed
          console.log(error)
          rej(error);
        },
        () => {
          // upload success
          upload.url = uploadTask.snapshot.downloadURL
          upload.name = upload.file.name
          let refKey = this.db.list('artists/' + this._authSrv.authState.uid + '/newPieces').push(upload).key;
          res({ upload, refKey });
        }
      );
    })
  }

  // Writes the file details to the realtime db
  private saveNewPieceData(upload: Upload) {
    this.db.list('artists/' + this._authSrv.authState.uid + '/newPieces').push(upload);
  }

  private saveFileData(upload: Upload) {
    this.db.list('/uploads' + upload.file.name).push(upload);
  }

  // Writes the file details to the realtime db
  private deleteFileData(key: string) {
    return this.db.list(`${this.basePath}/`).remove(key);
  }

  // Firebase files must have unique names in their respective storage dir
  // So the name serves as a unique key
  private deleteFileStorage(name:string) {
    const storageRef = firebase.storage().ref();
    storageRef.child(`${this.basePath}/${name}`).delete()
  }


}



