import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable, FirebaseObjectObservable } from 'angularfire2/database';
import { AuthService } from '../../core/auth.service';
import { WebService } from '../shared/web.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'my-pieces',
  templateUrl: './my-pieces.component.html',
  styleUrls: ['./my-pieces.component.scss']
})
export class MyPiecesComponent implements OnInit {

  public myPieces;
  public myPiecesArr = [];

  constructor(private _db: AngularFireDatabase,
              private _authService: AuthService,
              private _router: Router,
              private _route: ActivatedRoute,
              private _webSrv: WebService) { }

  ngOnInit() {
    if(this._authService.authState) {
      this.getArtistsPieces(this._authService.authState.uid);
    } else {
      this._authService.authChange.subscribe(res => {
        this.getArtistsPieces(res.uid);
      })
    }
  }

  getArtistsPieces(artistUid) {
    this.myPieces = this._db.object('/artists/' + artistUid + '/artPieces', {
      preserveSnapshot: true
    });
    this.myPieces.subscribe(pieces => {
      this.myPiecesArr = [];

      pieces.forEach(piece => {
        let pieceClean = piece.val()
        pieceClean['pieceKey'] = piece.key;

        for(var key in pieceClean.imgUrls) {
          if(pieceClean['img1'] == undefined) {
            pieceClean['img1'] = pieceClean.imgUrls[key];
          }
        }

        this.myPiecesArr.push(pieceClean);
      })
      // this.artists = snapshot.val()
      // this.showNewPieceObj = true;
    })
  }
}



