import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable, FirebaseObjectObservable } from 'angularfire2/database';
import { AuthService } from '../../core/auth.service';
import { WebService } from '../shared/web.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'settings',
  templateUrl: './settings.component.html',
  styleUrls: ['./settings.component.scss']
})
export class SettingsComponent implements OnInit {

  public showArtistProfileObj = false;
  public artistObj;
  public artistProfileObj = {};
  public artistSocialObj = {};

  constructor(private _db: AngularFireDatabase,
              private _authService: AuthService,
              private _router: Router,
              private _route: ActivatedRoute,
              private _webSrv: WebService) { }

  ngOnInit() {
    if(this._authService.authState) {
      this.getArtistInfo(this._authService.authState.uid);
    } else {
      this._authService.authChange.subscribe(res => {
        this.getArtistInfo(res.uid);
      })
    }
  }

  updateAccountDetails() {

    if(this.artistProfileObj['minBid'] < this.artistProfileObj['minSuggestedBid']) {

      let saveObj = {
        artistProfile: this.artistProfileObj,
        socialMediaLinks: this.artistSocialObj  
      }
  
      console.log(saveObj);
  
      this._webSrv.updateAccountDetails(this._authService.authState.uid, saveObj).subscribe(res => {
        // console.log(res);
        alert('Your account details have been updated!');
      })
    } else {
      alert('Min. bid must be less than Min. suggested bid.')
    }

  }

  // STARTER FNCS
  getArtistInfo(artistUid) {
    this.artistObj = this._db.object('/artists/' + artistUid, {
      preserveSnapshot: true
    });
    this.artistObj.subscribe(artistObjRaw => {
      // console.log(artistObjRaw.val())
      this.artistProfileObj = artistObjRaw.val()['artistProfile'];
      this.artistSocialObj = artistObjRaw.val()['socialMediaLinks'];
      this.showArtistProfileObj = true;

    })
  }
}



