import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute, Params } from '@angular/router';
import { AngularFireDatabase, FirebaseListObservable, FirebaseObjectObservable } from 'angularfire2/database';
import { AuthService } from '../../../core/auth.service';
import { WebService } from '../../shared/web.service';

@Component({
  selector: 'my-piece',
  templateUrl: './my-piece.component.html',
  styleUrls: ['./my-piece.component.scss']
})
export class MyPieceComponent implements OnInit {

  public isBid:boolean = false;

  public pieceObj:any = {};
  public artistUid;
  public pieceUid;
  public showDeleteModal = false;

  constructor(private _activatedRoute:ActivatedRoute,
              private _db: AngularFireDatabase,
              private _authService: AuthService,
              private _router: Router,
              private _route: ActivatedRoute,
              private _webSrv: WebService) { }

  ngOnInit() {
    if(this._authService.authState) {
      this.artistUid = this._authService.authState.uid;
      this.getPieceDetails(this.artistUid);
    } else {
      this._authService.authChange.subscribe(res => {
        this.artistUid = res.uid;
        this.getPieceDetails(this.artistUid);
      })
    }
  }

  // Starter Fncs
  getPieceDetails(authUid) {
    // subscribe to router event
    this._activatedRoute.params.subscribe((params: Params) => {
      this.pieceUid = params['pieceId'];
      this.getPieceDetailsDBCall(authUid, this.pieceUid)
    });
  }

  // Event fncs
  savePieceDetails() {
    console.log(this.artistUid, this.pieceUid, this.pieceObj);
    console.log('thats it')
    this._webSrv.editPieceDetails(this.artistUid, this.pieceUid, this.pieceObj)
    .subscribe(res => {
      console.log(res)
      alert("Updated")
    })
  }

  bidPriceToggle() {
    this.isBid = !this.isBid;
    this.pieceObj.isBid = this.isBid;
  }

  deletePieceModal() {
    this.showDeleteModal = true;
  }
  closeDeleteModal() {
    this.showDeleteModal = false;
  }
  deletePiece() {
    this._webSrv.deletePiece(this.artistUid, this.pieceUid)
    .subscribe(res => {
      console.log(res)
      alert("Deleted");
      this._router.navigate(['/my-pieces'])
    })
  }

  // Helper Fncs
  getPieceDetailsDBCall(authUid, pieceUid) {
      let pieceRef = this._db.object('/artists/' + authUid + '/artPieces/' + pieceUid, {
        preserveSnapshot: true
      });
      pieceRef.subscribe(piece => {
        this.pieceObj = piece.val();
        console.log(this.pieceObj)
        this.pieceObj.price = parseInt(this.pieceObj.price);
        if(this.pieceObj.isBid == true) {
          this.isBid = true;
        } else {
          this.isBid = false;
        }

        
        // get img
        this.pieceObj.img1 = this.pieceObj.imgUrls[Object.keys(this.pieceObj.imgUrls)[0]];
        // console.log(this.pieceObj.img1)
      })
  }

}



