import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable, FirebaseObjectObservable } from 'angularfire2/database';
import { AuthService } from '../../../core/auth.service';
import { WebService } from '../../shared/web.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'new-piece-details',
  templateUrl: './new-piece-details.component.html',
  styleUrls: ['./new-piece-details.component.scss']
})
export class NewPieceDetailsComponent implements OnInit {

  public isBid:boolean = false;

  public newPiece;
  public newPieceObj;
  public showNewPieceObj = false;

  public name = '';
  public description = '';
  public price:number = 0;

  public toggleSubmitPreloader = false;

  constructor(private _db: AngularFireDatabase,
              private _authService: AuthService,
              private _router: Router,
              private _route: ActivatedRoute,
              private _webSrv: WebService) { }

  ngOnInit() {
    if(this._authService.authState) {
      this.getArtistsNewPiece(this._authService.authState.uid);
    } else {
      this._authService.authChange.subscribe(res => {
        this.getArtistsNewPiece(res.uid);
      })
    }
  }

  submtiNewPieceDetails() {
    if(this.name != '' && this.description != '' && (this.price > 0 || this.isBid)) {
      this.toggleSubmitPreloader = true;
      console.log(this.name)
      console.log(this.description)
      console.log(this.price)
      console.log(this.newPieceObj);

      let obj = {
        artPiece: {
          category: 'Uncategorized',
          pieceDescription: this.description,
          pieceName: this.name,
          price: this.price,
          source: 'Upload'
        },
        artistUid: this._authService.authState.uid,
        imgUid: this._route.snapshot.queryParams['imgId'],
        imgUrl: this.newPieceObj.url
      }

      if(this.isBid) {
        obj.artPiece.price = null;
        obj.artPiece['isBid'] = true;
      }

      console.log(obj);

      this.newPieceObj = { name: ".", url: "." }
      this.showNewPieceObj = false;
      this._webSrv.submitNewPieceDetails(obj).subscribe(res => {
        console.log(res);
        this._router.navigate(['/my-pieces']);
      })
    } else {
      alert("Please comeplete the piece form.");
    }
  }

  // Starter fncs 
  getArtistsNewPiece(artistUid) {
    this.newPiece = this._db.object('/artists/' + artistUid + '/newPieces/' + this._route.snapshot.queryParams['imgId'], {
      preserveSnapshot: true
    });
    this.newPiece.subscribe(snapshot => {
      this.newPieceObj = snapshot.val()
      this.showNewPieceObj = true;
    })
  }

  bidPrice() {
    if(!this.isBid) {
      this.name = 'My Piece';
      this.description = 'Shared on ' + Date().split(" ").splice(1,3).join(' ');
    }
    this.isBid = !this.isBid;
  }
}



