import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './core/auth.guard';

import { UserLoginComponent } from './ui/user-login/user-login.component';
import { ItemsListComponent } from './items/items-list/items-list.component';
// import { UploadsListComponent } from './uploads/uploads-list/uploads-list.component';
import { ReadmePageComponent } from './ui/readme-page/readme-page.component';

import { DashboardComponent } from './ui/dashboard/dashboard.component';
import { MyPiecesComponent } from './ui/my-pieces/my-pieces.component';
import { MyPieceComponent } from './ui/my-pieces/my-piece/my-piece.component';
import { NewPieceComponent } from './ui/my-pieces/new-piece/new-piece.component';
import { PurchaseRequestsComponent } from './ui/purchase-requests/purchase-requests.component';
import { NewPieceDetailsComponent } from './ui/my-pieces/new-piece-details/new-piece-details.component';
import { IGUploadComponent } from './ui/igupload/igupload.component';
import { SettingsComponent } from './ui/settings/settings.component';

import { CoreModule } from './core/core.module'

const routes: Routes = [
  { path: '', redirectTo: '/my-pieces', pathMatch: 'full' },
  { path: 'login', component: UserLoginComponent, },

  { path: 'dashboard', component: DashboardComponent , canActivate: [AuthGuard]},
  { path: 'settings', component: SettingsComponent , canActivate: [AuthGuard]},
  { path: 'my-pieces', component: MyPiecesComponent, canActivate: [AuthGuard] },
  { path: 'my-piece/:pieceId', component: MyPieceComponent, canActivate: [AuthGuard] },
  { path: 'ig-upload', component: IGUploadComponent, canActivate: [AuthGuard] },
  { path: 'new-piece', component: NewPieceComponent, canActivate: [AuthGuard] },
  { path: 'new-piece-details', component: NewPieceDetailsComponent, canActivate: [AuthGuard] },
  { path: 'purchase-requests', component: PurchaseRequestsComponent, canActivate: [AuthGuard] },

  { path: 'items', component: ItemsListComponent, canActivate: [AuthGuard]},
  { path: 'uploads', loadChildren: './uploads/shared/upload.module#UploadModule' }
  // { path: 'uploads', component: UploadsListComponent, canActivate: [AuthGuard]},
];

@NgModule({
  imports: [RouterModule.forRoot(routes, {useHash: true})],
  exports: [RouterModule],
  providers: [AuthGuard]
})
export class AppRoutingModule { }



