import { Component, OnInit } from '@angular/core';
import { AngularFireDatabase, FirebaseListObservable, FirebaseObjectObservable } from 'angularfire2/database';
import { AuthService } from '../../core/auth.service';
import { WebService } from '../shared/web.service';
import { Router, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'purchase-requests',
  templateUrl: './purchase-requests.component.html',
  styleUrls: ['./purchase-requests.component.scss']
})
export class PurchaseRequestsComponent implements OnInit {

  public myRequests;
  public myRequestsArr = [];
  public noRequestsPanel = false;

  constructor(private _db: AngularFireDatabase,
              private _authService: AuthService,
              private _router: Router,
              private _route: ActivatedRoute,
              private _webSrv: WebService) { }

  ngOnInit() {
    if(this._authService.authState) {
      this.getArtistsRequests(this._authService.authState.uid);
    } else {
      this._authService.authChange.subscribe(res => {
        this.getArtistsRequests(res.uid);
      })
    }
  }

  getArtistsRequests(artistUid) {
    this.myRequests = this._db.object('/artists/' + artistUid + '/purchaseForms', {
      preserveSnapshot: true
    });
    this.myRequests.subscribe(requests => {
      this.myRequestsArr = [];

      requests.forEach(request => {
        let requestClean = request.val()
        requestClean['pieceKey'] = request.key;

        // for(var key in pieceClean.imgUrls) {
        //   if(pieceClean['img1'] == undefined) {
        //     pieceClean['img1'] = pieceClean.imgUrls[key];
        //   }
        // }

        this.myRequestsArr.push(requestClean);
      })
      // this.artists = snapshot.val()
      // this.showNewPieceObj = true;
      console.log(this.myRequestsArr);

      if(this.myRequestsArr.length == 0) {
        this.noRequestsPanel = true;
      }
    })
  }

}



